.App {
    text-align: center;
    font-family: 'Source Sans Pro', sans-serif;
}

/* Navigation Bar and Jumbotron */

.app-title {
    font-size: 30px;
    font-weight: bolder;
    border: 1px solid black;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    word-spacing: 3.5px;
}

.subscribe {
    text-transform: uppercase;
    letter-spacing: 1.5px;
    word-spacing: 2.5px;
    padding: 2rem;
    color: black;
    background-color: white;
    border: 1px solid black;
    font-size: 17.5px;
    cursor: pointer;
    -o-transition: .5s;
	-webkit-transition: .5s;
    transition: .5s;
}

.subscribe:hover {
    letter-spacing: 2px;
    word-spacing: 3px;
    padding: 2rem;
    color: gray;
    background-color: rgba(255, 255, 255, 0.5);
    font-size: 17.5px;
    -o-transition: .5s;
	-webkit-transition: .5s;
    transition: .5s;
}

/* Section Styling */

.section {
    height: auto;
    width: 100%;
    padding: 2rem;
}

.section-heading {
    font-size: 20px;
    font-weight: bolder;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    word-spacing: 2.5px;
    padding: 2rem 0;
}

.section-subheading {
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
    word-spacing: 2px;
    padding: 1.5rem 0;
}

.section-light-brown {
    background-color: rgb(229, 222, 211);
}

.section-light-pink {
    background-color: rgb(241, 207, 203);
}

.section-pink {
    background-color: rgb(243, 164, 157);
}

.section-image {
    /* background-image: url("./img/wec.jpg");
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: right top;
    background-size: cover; */
}

.section-h {
    height: 50%;
}

.box {
    background-color: rgba(0, 0, 0, 0.35);
    border: 2.5px solid black;
    padding: 2rem;
    margin: 2rem;
}

/* Board Memeber Section CSS */

.board-card {
    display: inline-block;
}

.board-picture {
    width: 200px;
    height: 200px;
    border-radius: 100%;
}

.board-name {
    color: black;
    letter-spacing: .5px;
    word-spacing: 1px;
    margin-top: 1rem;
}

.board-title {
    color: rgba(0, 0, 0, 0.5);
    letter-spacing: 1.5px;
    word-spacing: 2px;
    margin-top:1rem;
}

.row-spacing {
    padding: 2rem 0;
}

/* Multiple Lines of Written Content */

.mission {
    height: auto;
    letter-spacing: 1px;
    word-spacing: 2px;
    padding: 2rem 0;
}

/* Upcoming Events Section */

.when-where {
    height: auto;
    letter-spacing: 1px;
    word-spacing: 2px;
    line-height: 3rem;
    padding: 2rem;
}

.rsvp {
    text-transform: uppercase;
    letter-spacing: 1.5px;
    word-spacing: 2.5px;
    padding: 2rem;
    color: black;
    background-color: white;
    border: 1px solid black;
    font-size: 17.5px;
    cursor: pointer;
    -o-transition: .5s;
	-webkit-transition: .5s;
    transition: .5s;
}

.rsvp:hover {
    letter-spacing: 2px;
    word-spacing: 3px;
    padding: 2rem;
    color: gray;
    background-color: rgba(255, 255, 255, 0.5);
    font-size: 17.5px;
    -o-transition: .5s;
	-webkit-transition: .5s;
    transition: .5s;
}

/* Footer CSS */

.icon {
    padding: 2rem;
    color: black;
    -o-transition: .5s;
	-webkit-transition: .5s;
    transition: .5s;
}

.icon:hover {
    color: gray;
    -o-transition: .5s;
	-webkit-transition: .5s;
    transition: .5s;
}


